//Fonts
@import url(https://fonts.googleapis.com/css?family=Droid+Sans:400,700);

@font-awesome: 'Font Awesome';
@font-face {
	font-family: @font-awesome;
	src: url('fonts/fontawesome.eot');
	src: url('fonts/fontawesome.eot?#iefix') format('embedded-opentype'),
	url('fonts/fontawesome.svg') format('svg'),
	url('fonts/fontawesome.woff') format('woff');
}

//Global Colors
@color-white: #fff;
@color-black: #000;

//Media Queries
.media-mobile(@styles) {
	@media screen and (max-width: 768px) {
		@styles();
	}
}

.media-tablet(@styles) {
	@media screen and (max-width: 1024px) {
		@styles();
	}
}


//Functions
.clear {
	&:after {
		clear: both;
		content: '';
		display: table;
	}
}

.awesome-icon(@charcode, @position: after, @margin-top: null, @margin-right: null, @margin-bottom: null, @margin-left: null, @margin: null) {
	& when (@position = after) {
		&:after {
			font-family: @font-awesome;
			font-style: normal;
			content: @charcode;

			&when not(@margin-top = null) {
				margin-top: @margin-top;
			}

			&when not(@margin-right = null) {
				margin-right: @margin-right;
			}

			&when not(@margin-bottom = null) {
				margin-bottom: @margin-bottom;
			}

			&when not(@margin-left = null) {
				margin-left: @margin-left;
			}

			&when not(@margin = null) {
				margin: @margin;
			}
		}
	}

	&when (@position = before) {
		&:before {
			font-family: @font-awesome;
			font-style: normal;
			content: @charcode;

			&when not (@margin-top = null) {
				margin-top: @margin-top;
			}

			&when not (@margin-right = null) {
				margin-right: @margin-right;
			}

			&when not (@margin-bottom = null) {
				margin-bottom: @margin-bottom;
			}

			&when not (@margin-left = null) {
				margin-left: @margin-left;
			}

			&when not (@margin = null) {
				margin: @margin;
			}
		}
	}
}

.user-select(@value) {
	-webkit-user-select: @value;
	-moz-user-select: @value;
	-khtml-user-select: @value;
	-ms-user-select: @value;
}

.appearance(@value) {
	-webkit-appearance: @value;
	-moz-appearance: @value;
	appearance: @value;
}

.transition(@value) {
	-webkit-transition: @value;
	-moz-transition: @value;
	transition: @value;
}

.transform(@value) {
	-webkit-transform: @value;
	-moz-transform: @value;
	transform: @value;
}

.filter(@value) {
	-webkit-filter: @value;
	-moz-filter: @value;
	filter: @value;
}

.animation(@name, @duration: null, @delay: null, @direction: null, @iterationCount: null, @fillMode: null, @timingFunction: null) {
	-moz-animation-name: @name;
	-webkit-animation-name: @name;
	animation-name: @name;

	&when not (@duration = null) {
		-moz-animation-duration: @duration;
		-webkit-animation-duration: @duration;
		animation-duration: @duration;
	}

	&when not (@delay = null) {
		-moz-animation-delay: @delay;
		-webkit-animation-delay: @delay;
		animation-delay: @delay;
	}

	&when not (@direction = null) {
		-moz-animation-direction: @direction;
		-webkit-animation-direction: @direction;
		animation-direction: @direction;
	}

	&when not (@iterationCount = null) {
		-moz-animation-iteration-count: @iterationCount;
		-webkit-animation-iteration-count: @iterationCount;
		animation-iteration-count: @iterationCount;
	}

	&when not (@fillMode = null) {
		-moz-animation-fill-mode: @fillMode;
		-webkit-animation-fill-mode: @fillMode;
		animation-fill-mode: @fillMode;
	}

	&when not (@timingFunction = null) {
		-moz-animation-timing-function: @timingFunction;
		-webkit-animation-timing-function: @timingFunction;
		animation-timing-function: @timingFunction;
	}
}

.keyframes(@name, @steps) {
	@-moz-keyframes @name {
		@steps();
	}

	@-webkit-keyframes @name {
		@steps();
	}

	@keyframes @name {
		@steps();
	}
}

.slide-in-down {
	.ready &{
		.animation(@name: slideInDown, @duration: 5s, @fillMode: both);

		.keyframes(@name: slideInDown, @steps: {
			to {
				margin-top: -26%;
			}
		});
	}
}